module.exports = "<div class=markdown><h1 id=button>Button</h1> <hr> <p>按钮用于开始一个即时操作。</p> <h2 id=->何时使用</h2> <p>标记了一个（或封装一组）操作命令，响应用户点击行为，触发相应的业务逻辑。</p> <h2 id=->如何使用</h2> <ul> <li><p>通过设置 Button 的属性来产生不同的按钮样式，推荐顺序为：<code>type</code> -&gt; <code>shape</code> -&gt; <code>size</code> -&gt; <code>loading</code> -&gt; <code>disabled</code></p> </li> <li><p>按钮的属性说明如下：</p> </li> </ul> <table> <thead> <tr> <th>属性</th> <th>说明</th> <th>类型</th> <th>默认值</th> </tr> </thead> <tbody> <tr> <td>type</td> <td>设置按钮类型，可选值为 <code>primary</code> <code>default</code> 或者不设</td> <td>Enum</td> <td>undefined</td> </tr> <tr> <td>htmlType</td> <td>设置 <code>button</code> 原生的 <code>type</code> 值，可选值请参考 HTML标准</td> <td>Enum</td> <td><code>button</code></td> </tr> <tr> <td>shape</td> <td>设置按钮形状，可选值为 <code>circle</code> <code>circle-outline</code> 或者不设</td> <td>Enum</td> <td>undefined</td> </tr> <tr> <td>size</td> <td>设置按钮大小，可选值为 <code>xsmall</code> <code>small</code> <code>large</code> 或者不设</td> <td>Enum</td> <td>undefined</td> </tr> <tr> <td>loading</td> <td>设置按钮载入状态，存在为 <code>true</code>，不存在为 <code>false</code>，或直接设置值，如：<code>loading=&quot;true&quot;</code></td> <td>Bool</td> <td>false</td> </tr> <tr> <td>onClick</td> <td><code>click</code> 事件的 handler</td> <td>Function</td> <td><code>function() {}</code></td> </tr> </tbody> </table> <ul> <li>除了上表中的属性，其它属性都会直接传到 <code>&lt;button&gt;&lt;/button&gt;</code></li> </ul> <h2 id=->组件演示</h2> <div class=code-boxes> <div class=code-box> <h3>按钮类型</h3> <div class=\"code-boxes-col-2-1 code-box-demo\"> <v-button type=primary>主按钮</v-button> <v-button>次按钮</v-button> <v-button type=default>幽灵按钮</v-button> </div> <div class=\"code-boxes-col-2-1 code-box-code\"><pre>\n<span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>v-button</span> <span class=\"token attr-name\">type</span><span class=\"token attr-value\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">\"</span>primary<span class=\"token 