module.exports = "<div class=markdown><h1 id=layout->Layout 布局</h1> <hr> <p>24 栅格系统。</p> <p></p><div class=\"row demo-row\"> <div class=\"col-md-24 demo-col demo-col-1\"> 100% </div> </div><p></p> <p></p><div class=\"row demo-row\"> <div class=\"col-md-6 demo-col demo-col-2\"> 25% </div> <div class=\"col-md-6 demo-col demo-col-3\"> 25% </div> <div class=\"col-md-6 demo-col demo-col-2\"> 25% </div> <div class=\"col-md-6 demo-col demo-col-3\"> 25% </div> </div><p></p> <p></p><div class=\"row demo-row\"> <div class=\"col-md-8 demo-col demo-col-4\"> 33.33% </div> <div class=\"col-md-8 demo-col demo-col-5\"> 33.33% </div> <div class=\"col-md-8 demo-col demo-col-4\"> 33.33% </div> </div><p></p> <p></p><div class=\"row demo-row\"> <div class=\"col-md-12 demo-col demo-col-1\"> 50% </div> <div class=\"col-md-12 demo-col demo-col-3\"> 50% </div> </div><p></p> <p></p><div class=\"row demo-row\"> <div class=\"col-md-16 demo-col demo-col-4\"> 66.66% </div> <div class=\"col-md-8 demo-col demo-col-5\"> 33.33% </div> </div><p></p> <h2 id=->设计理念</h2> <p>在多数业务情况下，EDM需要在设计区域内解决大量信息收纳的问题，因此在12栅格系统的基础上，我们将整个设计建议区域按照24等分的原则进行划分。</p> <p>划分之后的信息区块我们称之为“盒子”。建议横向排列的盒子数量最多四个，最少一个。“盒子”在整个屏幕上占比见上图。设计部分基于盒子的单位定制盒子内部的排版规则，以保证视觉层面的舒适感。</p> <h2 id=->概述</h2> <p>布局的栅格化系统，我们是基于行（row）和列（col）来定义信息区块的外部框架，以保证页面的每个区域能够稳健地排布起来。下面简单介绍一下它的工作原理：</p> <ul> <li>通过<code>row</code>在水平方向建立一组<code>column</code>（简写col）</li> <li>你的内容应当放置于<code>col</code>内，并且，只有<code>col</code>可以作为<code>row</code>的直接元素</li> <li><code>col-xs</code>,<code>col-sm</code>,<code>col-md</code>,<code>col-lg</code>分别对应栅格中对应<code>小于768px</code>,<code>大于768px</code>,<code>大于992px</code>,<code>大于1200px</code>对于屏幕宽度的区分</li> <li>栅格系统中的列是指1到24的值来表示其跨越的范围。例如，三个等宽d的列可以使用<code>.col-md-8</code>来创建</li> <li>如果一个<code>row</code>中的<code>col</code>总和超过24，那么多余的<code>col</code>会作为一个整体另起一行排列</li> </ul> <h2 id=->组件演示</h2> <div class=code-boxes> <div class=code-box> <h3>基础布局</h3> <div class=\"code-boxes-col-2-1 code-box-demo\"> <div class=row> <v-col span=12 type=md>.col-md-12</v-col> <v-col span=12 type=md>.col-md-12</v-col> </div> <div class=row> <v-col span=8 type=sm>.col-sm-8</v-col> <v-col span=8 type=sm>.col-sm-8</v-col> <v-col span=8 type=sm>.col-sm-8</v-col> </div> <div class=row> <v-col span=6 type=md>.col-md-6</v-col> <v-col span=6 type=md>.col-md-6</v-col> <v-col span=6 type=md>.col-md-6</v-col> <v-col span=6 type=md>.col-md-6</v-col> </div> </div> <div class=\"code-boxes-col-2-1 code-box-code\"><pre>\n<span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>div</span> <span class=\"token attr-name\">class</span><span class=\"token attr-value\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">\"</span>row<span class=\"token punctuation\">\"</span></span><span class=\"token punctuation\">></span></span>\n  <span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>v-col</span> <span class=\"token attr-name\">span</span><span class=\"token attr-value\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">'</span>12<span class=\"token punctuation\">'</span></span> <span class=\"token attr-name\">type</span><span class=\"token attr-value\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">'</span>md<span class=\"token punctuation\">'</span></span><span class=\"token punctuation\">></span></span>.col-md-12<span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;/</span>v-col</span><span class=\"token punctuation\">></span></span>\n  <span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>v-col</span> <span class=\"token attr-name\">span</span><span class=\"token attr-value\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">'</span>12<span class=\"token punctuation\">'</span></span> <span class=\"token attr-name\">type</span><span class=\"token attr-value\"><span class