module.exports = "<div class=markdown><h1 id=upload>Upload</h1> <hr> <p>文件选择上传和拖拽上传控件。</p> <h2 id=->何时使用</h2> <p>上传是将信息（网页、文字、图片、视频等）通过网页或者上传工具发布到远程服务器上的过程。</p> <ul> <li>当需要上传一个或一些文件时。</li> <li>当需要展现上传的进度时。</li> <li>当需要使用拖拽交互时。</li> </ul> <h2 id=api>API</h2> <table> <thead> <tr> <th>参数</th> <th>说明</th> <th>类型</th> <th>默认值</th> </tr> </thead> <tbody> <tr> <td>name</td> <td>可选参数, 上传的文件</td> <td>String</td> <td>file</td> </tr> <tr> <td>action</td> <td>必选参数, 上传的地址</td> <td>String</td> <td>无</td> </tr> <tr> <td>data</td> <td>可选参数, 上传所需参数</td> <td>Object</td> <td>无</td> </tr> <tr> <td>multiple</td> <td>可选参数, 是否支持多选文件，支持 <code>ie10+</code></td> <td>Boolean</td> <td>false</td> </tr> <tr> <td>accept</td> <td>可选参数, 接受上传的文件类型, 详见 input accept Attribute</td> <td>String</td> <td>无</td> </tr> <tr> <td>onChange</td> <td>可选参数, 上传文件改变时的状态，详见 onChange</td> <td>Function</td> <td>无</td> </tr> </tbody> </table> <h3 id=onchange>onChange</h3> <p>文件状态改变的回调，返回为：</p> <pre><code class=lang-js>{\n  file: { ... },\n  fileList: [ ... ],\n  event: { ... }\n}\n</code></pre> <p><code>file</code> 当前操作的文件对象。</p> <pre><code class=lang-js>{\n  uid: &#39;uid&#39;,      // 文件唯一标识，建议设置为负数，防止和内部产生的 id 冲突\n  name: &#39;xx.png&#39;   // 文件名\n  status: &#39;done&#39;,  // 状态有：uploading done error removed\n  response: &#39;{&quot;status&quot;:&quot;success&quot;}&#39;  // 服务端响应内容\n}\n</code></pre> <p>如果上传控件是 multiple 时，此参数将为一个对象数组 <code>[file, ...]</code>。</p> <p><code>fileList</code> 当前的文件列表。 <code>event</code> 上传中的服务端响应内容，包含了上传进度等信息，高级浏览器支持。</p> <h2 id=->显示下载链接</h2> <p>请使用 fileList 属性设置数组项的 url 属性进行展示控制。</p> <h2 id=ie-note>IE note</h2> <ul> <li><a href=https://github.com/react-component/upload#ie89-note>https://github.com/react-component/upload#ie89-note</a></li> </ul> <h2 id=->组件演示</h2> </div>";


