	module.exports = "<div class=markdown><h1 id=checkbox>Checkbox</h1> <hr> <p>多选框。</p> <h2 id=->何时使用</h2> <ul> <li>需要表示开关状态/两种状态之间的切换时；</li> <li>和 <code>switch</code>的区别是，切换 <code>switch</code> 会直接触发状态改变，而 <code>checkbox</code> 一般用于状态标记，需要和提交操作配合。</li> </ul> <h2 id=api>API</h2> <h3 id=checkbox>Checkbox</h3> <table> <thead> <tr> <th>参数</th> <th>说明</th> <th>类型</th> <th>可选值</th> <th>默认值</th> </tr> </thead> <tbody> <tr> <td>checked</td> <td>指定当前是否选中</td> <td>boolean</td> <td></td> <td>false</td> </tr> <tr> <td>default-checked</td> <td>初始是否选中</td> <td>boolean</td> <td></td> <td>false</td> </tr> <tr> <td>on-change</td> <td>变化时回调函数</td> <td>Function(e:Event)</td> <td></td> <td></td> </tr> </tbody> </table> <h2 id=->组件演示</h2> </div>";

