	module.exports = "<div class=markdown><h1 id=->下载</h1> <hr> <p>可以使用 npm 进行安装或直接下载文件。</p> <h2 id=-npm->从 npm 安装</h2> <p><strong>我们推荐使用 npm 的方式进行开发</strong>，不仅可在开发环境轻松调试，也可放心地在生产环境打包部署使用，享受整个生态圈和工具链带来的诸多好处。</p> <p>可以通过 npm 直接安装到本地，使用 <code>require</code> 或 <code>import</code> 进行引用。</p> </div>";

