	module.exports = "<div class=markdown><h1 id=->快速上手</h1> <hr> <h2 id=->标准项目</h2> <p>实际项目开发中，你会需要 CommonJS 、JSX 构建、打包部署等一系列工程化的需求。 提供了一套 <code>npm</code> + <code>webpack</code> 的开发工具链来辅助开发，下面我们用一个简单的实例来说明。</p> <h3 id=1->1. 使用组件</h3> <h3 id=2->2. 开发调试</h3> <p>一键启动调试，访问 <a href=http://127.0.0.1:8000>http://127.0.0.1:8000</a> 查看效果。</p> <pre><code class=lang-bash>$ npm run dev\n</code></pre> <h3 id=3->3. 构建和部署</h3> <pre><code class=lang-bash>$ npm run build\n</code></pre> <p>入口文件会构建到 <code>dist</code> 目录中，你可以自由部署到不同环境中进行引用。</p> <blockquote> <p>上述例子用于帮助你理解 EDM Vue Component 的使用流程，并非真实的开发过程，你可以根据自己的项目开发流程进行接入。</p> </blockquote> <h2 id=->兼容性</h2> <p>EDM Vue Component 支持所有的现代浏览器（IE9+）。</p> <h2 id=->小甜点</h2> <ul> <li>你可以享用 <code>npm</code> 生态圈里的所有模块。</li> <li>我们使用了 <code>babel</code> 的写法来提升编码的愉悦感。</li> </ul> </div>";

